
@import url('../static/css/reset.css');
@import url('../static/fonts/iconfont.css');
html,body,#wrap,.app-main{
    width: 100%;
    height: 100%;
    background: #f2f2f2;
}
.app-footer{
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 50px;
    display: -ms-flexbox;
    display: flex;
    border-top: 1px solid #ccc;
    background: white;
    z-index: 100;
}
.app-footer>a{
    -ms-flex: 1;
        flex: 1;
    display: block;
    text-align: center;
    height: 100%;
    font-size: 14px;
    color: #808080;
}
.app-footer>a>i{
    display: block;
    font-size: 25px;
    padding-top: 3px;
}
.app-footer>a>span{
    display: block;
    font-size: 14px;
}
.app-footer>.active{
    color: #00BA19;
}



