
.details{
	position: relative;
	width: 100%;
	height: 100%;
	background: #f2f2f2;
	z-index: 1000;
}
/*头部*/
.details .header>div>i{
	top: 0px;
	font-size: 28px;
	position: fixed;
}
.icon-fanhui{
	left: 10px;
}
.icon-shoucang1{
	right: 50px;
}
.icon-zhuanfa{
	right: 10px;
	top: -3px !important;
}
/*中间*/
.details-content{
	width: 100%;
	padding-top: 42px;
	background: #f2f2f2;
}
.details-content>img{
	width: 100%;
}
.details-content>.text{
	width: 100%;
	padding: 10px 10px 15px 10px;
	background: white;
	box-sizing: border-box;
}
.details-content>.text>h3{
	width: 100%;
	font-size: 16px;
}
.details-content>.text>p{
	width: 100%;
	font-size: 14px;
	color: #666666;
	padding: 8px 0 10px 0px;
	border-bottom:1px solid #CEDCE2;
}
.details-content>.text>div{
	width: 100%;
	font-size: 14px;
	color: #666666;
	padding: 8px 0 10px 0px;
}
.goods-msg{
	width: 100%;
	margin-top: 10px;
	display: -ms-flexbox;
	display: flex;
	border-bottom:1px solid #e0e0e0;
}
.goods-msg>li{
	-ms-flex: 1;
	    flex: 1;
	height: 42px;
	text-align: center;
	background: white;
	line-height: 42px;
	font-size: 16px;
	position: relative;
	color: #666666;
}
.line{
	border-bottom: 2px solid #ff3f12;
	margin-top: -2px;
}
.s-page,.y-page{
	width: 100%;
	padding-bottom: 42px;
}
.s-page>li,.y-page>li{
	width: 100%;
}
.s-page>li>img{
	display: inline-block;
	width: 100%;
}
.s-page>li,.y-page>li,.y-page-con{
	width: 100%;
	padding: 5px;
	box-sizing: border-box;
}
.y-page-con>li{
	padding: 10px;
	margin-top: 10px;
}
.y-page-con>li>h3{
	font-size: 14px;
	padding-bottom: 5px;
	border-bottom:1px solid #B6C1C9;
}
.y-page-con>li>p{
	display: inline-block;
	font-size: 14px;
	color: #99C9C3;
	margin-top: 10px;
}
.goods-footer{
	position: fixed;
	bottom: 0px;
	width: 100%;
	height: 42px;
	background: white;
}
.goods-footer>i{
	display: inline-block;
	position: relative;
	width: 40px;
	height: 33px;
	font-size: 35px;
	left: 20px;
	top: 2px;
}
.goods-footer>span{
	float: right;
	width: 100px;
	height: 30px;
	margin: 6px 10px;
	font-size: 16px;
	color: white;
	border-radius: 3px;
	text-align: center;
	line-height: 30px;
}
.ok{
	position: absolute;
	top: 0px;
	right: 0px;
	background: #ee3749;
	width: 18px;
	height: 18px;
	border-radius: 50%;
	text-align: center;
	line-height: 20px;
	font-size: 14px;
	color: white;
}
.goods-footer>span:nth-child(2){
	background: #f29004;
}
.goods-footer>span:nth-child(3){
	background: #ee3749;
}
.car{
	width: 100%;
	height: 0px;
	background: white;
	position: fixed;
	bottom: 0px;
	z-index: 1009;
	transition: all .5s;
	box-shadow: 0 0 1px 1px rgb(242, 229, 237);
}
.car>.close{
	width: 20px;
	height: 20px;
	position: absolute;
	top: 5px;
	right: 5px;
	font-size: 25px;
	color: #666666;
}
.carChange{
	height: 350px;
}
.car-top{
	display: -ms-flexbox;
	display: flex;
	width: 100%;
	height: 100px;
}
.car-top>img{
	display: inline-block;
	-ms-flex:0 0 100px;
	    flex:0 0 100px;
	height: 100px;
	top: -10px;
	box-shadow: 0px 0px 2px 2px rgb(224, 224, 224);
	border-radius: 5px;
}
.car-top>p{
	-ms-flex: 1;
	    flex: 1;
	height: 100px;
	padding-left: 20px;
	box-sizing: border-box;
}
.car-top>p>span{
	display: block;
	font-size: 14px;
	color: #666666;
	margin-top: 20px;
}
.car-text-red{
	color:red !important;
}
.car-size{
	width: 100%;
	background: white;
	height: 70px;
	border-top: 1px solid rgb(222, 222, 222);
	padding: 5px;
	box-sizing: border-box;
	margin-top: 2px;
}
.car-size>p{
	width: 100%;
	height: 30px;
	line-height: 30px;
}
.car-size>ul{
	width: 100%;
	height: 30px;
	display: -ms-flexbox;
	display: flex;
}
.car-size>ul>li{
	-ms-flex: 1;
	    flex: 1;
	background: rgb(185, 185, 185);
	border-radius: 5px;
	margin-left: 2px;
	text-align: center;
	line-height: 30px;
	font-size: 14px;
}
.car-color{
	width: 100%;
	height: 70px;
	padding: 5px;
	box-sizing: border-box;
}
.car-color>p{
	width: 100%;
	height: 30px;
	line-height: 30px;
}
.car-color>ul{
	width: 100%;
	height: 30px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: left;
	    justify-content: left;
}
.car-color>ul>li{
	-ms-flex: 0 0 40px;
	    flex: 0 0 40px;
	background: rgb(185, 185, 185);
	margin-left: 2px;
	border-radius: 2px;
	text-align: center;
	line-height: 30px;
}
.car-number{
	width: 100%;
	height: 40px;
	padding: 5px;
	box-sizing: border-box;
	border-top: 1px solid rgb(222, 222, 222);
	display: -ms-flexbox;
	display: flex;
}
.car-number>span{
	background: rgb(185, 185, 185);
	-ms-flex: 1;
	    flex: 1;
}
.car-number>.number1{
	-ms-flex: 3;
	    flex: 3;
	background: white;
}
.car-number>.number2,.car-number>.number4{
	-ms-flex: 0 0 30px;
	    flex: 0 0 30px;
	height: 30px;
	border-radius: 2px;
	text-align: center;
	line-height: 30px;
}
.car-number>.number3{
	-ms-flex: 0 0 30px;
	    flex: 0 0 30px;
	height: 30px;
	background: white;
	text-align: center;
	line-height: 30px;
}
.car-ok{
	display: inline-block;
	text-align: center;
	line-height: 40px;
	font-size: 16px;
	color: white;
	width: 100%;
	height: 40px;
	background: rgb(236, 113, 12);
}



