
.my {
  width: 100%;
  height: 100%;
  background: #f2f2f2;
  padding-bottom: 50px;
  box-sizing: border-box;
}
.my-content {
  width: 100%;
  padding-top: 42px;
}
.my-header {
  width: 100%;
  height: 200px;
  background: #dedede;
  text-align: center;
}
.my-header > img {
  width: 100px;
  height: 100px;
  margin-top: 50px;
  border-radius: 50%;
}
.my-list {
  width: 100%;
  margin-top: 3px;
}
.my-list > li {
  position: relative;
  width: 100%;
  height: 30px;
  border-bottom: 1px solid #c4c4c4;
  font-size: 14px;
  line-height: 30px;
}
.i {
  font-size: 20px;
  color: green;
  position: absolute;
  top: 0px;
  left: 10px;
}
.my-list > li > span {
  display: inline-block;
  margin-left: 50px;
  color: #666666;
}
.i1 {
  float: right;
  margin-right: 10px;
  color: #666666;
}



