
.goods {
  width: 100%;
  height: 100%;
}
.goods-content {
  width: 100%;
  height: 100%;
  padding: 42px 0px 50px 0px;
  box-sizing: border-box;
}
.goods .list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  width: 100%;
  background: red;
  background: #f2f2f2;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding-bottom: 42px;
}
.goods .list > li {
  display: inline-block;
  -ms-flex-preferred-size: 48%;
      flex-basis: 48%;
  margin-top: 5px;
  height: 250px;
  background: white;
  text-align: left;
  box-shadow: 0 0 2px 2px #E4E7E5;
}
.goods .list > li img {
  display: block;
  width: 100%;
  height: 150px;
}
.goods .list > li p {
  font-size: 16px;
  line-height: 25px;
  padding: 10px;
}
.goods .list > li h3 {
  display: inline-block;
  font-size: 16px;
  padding: 0px 10px 0px 10px;
  color: red;
  font-weight: bolder;
}
.goods .list > li span {
  color: #666666;
  text-decoration: line-through;
  font-size: 14px;
}
/*设置头部*/
.goods .back {
  position: absolute;
  color: white;
  font-size: 30px;
  left: 8px;
  line-height: 42px;
}
.goods .search {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.search > input {
  -ms-flex: 0 0 55%;
      flex: 0 0 55%;
  height: 22px;
  padding: 3px 10px;
  font-size: 14px;
  outline-style: none;
}
.search > i {
  -ms-flex: 0 0 30px;
      flex: 0 0 30px;
  height: 28px;
  color: #868882;
  background: white;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
}



