
.shop {
  width: 100%;
  height: 100%;
  background: #f2f2f2;
  padding-bottom: 50px;
  box-sizing: border-box;
}
.header > .editor {
  position: fixed;
  top: 6px;
  right: 6px;
  width: 60px;
  height: 30px;
  border: 1px solid white;
  line-height: 30px;
  border-radius: 3px;
}
.shop-content {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 42px 0px 0px 0px;
}
.shop-list {
  width: 100%;
  padding: 5px;
  box-sizing: border-box;
}
.shop-list > li {
  width: 100%;
  height: 90px;
  margin-top: 5px;
  box-shadow: 0 0 1px 1px #e0e0e0;
  position: relative;
}
.shop-list > li > .check {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 25px;
  height: 25px;
  z-index: 1;
}
.shop-list > li > .check > img {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
.shop-list > li > img {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100px;
  height: 100%;
}
.shop-list > li > .txt {
  display: inline-block;
  height: 100%;
  width: 180px;
  position: absolute;
  top: 0;
  left: 105px;
  line-height: 20px;
  font-size: 14px;
  padding-top: 10px;
  box-sizing: border-box;
  color: #666666;
}
.txt > .p2 {
  text-decoration: line-through;
}
.txt > .p3 {
  color: red;
}
.editor-con {
  position: absolute;
  right: 0px;
  top: 0px;
  height: 100%;
  width: 80px;
  text-align: center;
}
.editor-con > li {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 30px;
  top: 30px;
}
.editor-number > span {
  display: inline-block;
  height: 20px;
  width: 20px;
  margin-top: 5px;
  line-height: 20px;
  border: 1px solid #d6d6d6;
  border-radius: 50%;
}
.editor-number > .number {
  color: red;
}
.shop-footer {
  position: fixed;
  bottom: 50px;
  padding: 5px;
  box-sizing: border-box;
  width: 100%;
  height: 60px;
  background: #d7d7d7;
  z-index: 1010;
}
.shop-footer > .check {
  height: 50px;
  width: 50%;
}
.shop-footer > .check > img {
  position: absolute;
  width: 25px;
  height: 25px;
  top: 15px;
  right: 0px;
}
.shop-footer > .check > p {
  position: absolute;
  top: 20px;
  right: 30px;
  color: #666666;
}
.shop-footer > .sum {
  position: absolute;
  top: 13px;
  left: 120px;
  color: #666666;
  font-size: 14px;
}
.del {
  position: absolute;
  top: 15px;
  right: 80px;
  width: 80px;
  height: 30px;
  background: red;
  color: white;
  font-size: 14px;
  text-align: center;
  line-height: 30px;
  border: 1px solid #b0501a;
}
.maidan {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100px;
  height: 100%;
  background: #e37b00;
  text-align: center;
  line-height: 60px;
  color: white;
  font-size: 18px;
}



