
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s;
}
.fade-enter {
  opacity: 0;
}
.fade-leave-active {
  display: none;
}
.home {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.home-content {
  width: 100%;
  padding-top: 42px;
}
/*按钮区*/
.home-nav {
  width: 100%;
  padding: 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  box-sizing: border-box;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.home-nav > li {
  -ms-flex: 1;
      flex: 1;
  height: 100%;
  margin-top: 3px;
  text-align: center;
}
.home-nav > li > a {
  width: 100%;
  height: 100%;
}
.home-nav > li > a > i {
  display: inline-block;
  width: 50px;
  height: 50px;
  font-size: 30px;
  color: white;
  background: #00BA19;
  border-radius: 50%;
  text-align: center;
  line-height: 50px;
}
.home-nav > li > a > p {
  padding-top: 10px;
  font-size: 14px;
}
.home-msg {
  font-size: 16px;
  color: #808080;
  text-align: center;
  height: 30px;
  line-height: 30px;
  position: relative;
}
.home-msg > span {
  background: #f2f2f2;
  position: relative;
  z-index: 2;
}
.home-msg:after {
  width: 100%;
  content: "";
  position: absolute;
  left: 0;
  top: 14px;
  height: 1px;
  background: #e0e0e0;
}
.home-list {
  width: 100%;
  height: 200px;
  padding: 5px;
  box-sizing: border-box;
}
.home-list > li {
  width: 100%;
}
.home-list > li > img {
  display: inline-block;
  width: 100%;
}
.home-list > li > p {
  font-size: 16px;
  padding: 5px 10px 20px 10px;
}



